body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

i{
  cursor: pointer;
}

.feedback_container {
  font-family: "overpass", "Signika negative";
  width: 100%;
  padding: 2rem;
  min-height: 100vh;
  transition: all 0.4s ease-in;
  position: absolute;
  background-color: white;
  top: 0;
  z-index: 60;
}
.close_button{
  position: relative;
  left: 10rem;
  background-color: rgb(216, 199, 216);
  text-align: center;
  width: 1.7rem;
  font-size: 1.6rem;
  height: 1.6rem;
  border-radius: 10%;
}
.close_button:hover{
  background-color: rgb(219, 205, 219);
}
.feedback_circle {
  flex-direction: column;

  display: flex;
  justify-content: center;
  align-items: center;
}
.feedback_form {
  width: 22rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* background-color: red; */
  border: 2px solid gray;
  border-radius: 10px;
  padding: 2rem;
}
.feedback_heading {
  font-size: 1.5rem;
  text-align: center;
  font-weight: bold;
}
.star_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 1rem 0;
}
.stars_box {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 0.2rem 0;
}
.stars_box span {
  font-size: 1.3rem;
}
.stars {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 0.3rem;
  gap: 0.3rem;
}
.fa-star {
  font-size: 1.5rem;
}
/* comments box */
.feedback_comment {
  width: 100%;
}
#comment_textarea {
  width: 100%;
  max-height: 4rem;
  overflow-y: scroll;
  padding: 0.2rem 0.5rem;
}
/* image upload box */
.feedback_image {
  width: 100%;
}
.low_heading {
  font-size: 1.2rem;
}
#upload_image_label {
  /* background-color: rgb(157, 165, 207); */
  padding: 0.3rem 0;
  background: linear-gradient(
    to right,
    rgb(154, 151, 202),
    rgb(141, 194, 207),
    rgb(121, 121, 187)
  );
  width: 100%;
  color: red;
  text-align: center;
  cursor: pointer;
  border-radius: 0.2rem;
  border: none;
  transition: 0.4s ease-in;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  font-weight: bold;
}

.feedback_submit {
  font-weight: bold;
  padding: 0.4rem 0;
  font-size: 1rem;
  margin: 1.5rem 0 0 0;
  background-color: rgb(141, 192, 107);
  width: 100%;
  text-align: center;
  cursor: pointer;
  border-radius: 0.2rem;
  border: none;
  transition: 0.4s ease-in;
}
.feedback_submit:hover {
  background-color: rgb(169, 216, 140);
}

@media (max-width: 400px) {
  .feedback_form {
    padding: 1rem;
  }
  .stars {
    position: relative;
    top: -4px;
    grid-gap: 0.1rem;
    gap: 0.1rem;
  }
}
@media (max-width: 336px) {
  .stars_box {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
  }
}

/* ADD TO CARD COMPONENT STYLES */

.addtocart-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.all-cart-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 2vw;
  gap: 2vw;
}
.cart-container {
  width: 30rem;
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  background-color: #f2f2f2;
  border: 1px solid gray;
}
.add-quantity {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 0.3rem;
  gap: 0.3rem;
}
.quantity {
  font-family: "signika negative";
  font-size: 1.1rem;
}
.cart-icons {
  font-size: 1.4rem;
  cursor: pointer;
}
.fa-trash {
  color: red;
  margin-left: 0.5rem;
}
.addtocart-container .cart-container img {
  width: 260px;
  height: 200px;
}
.cart-values {
  font-weight: 500;
}
.image-description {
  display: flex;
  justify-content: start;
  align-items: start;
  flex-direction: column;
}
.product-name,
.product-price,
.product-quantity {
  font-family: "signika negative";
  font-size: 1.3rem;
}
.payment-calculation {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 1rem;
  grid-gap: 0.7rem;
  gap: 0.7rem;
}
.pay-total {
  font-size: 1.5rem;
}
.pay-button:hover {
  background-color: rgb(113, 214, 113);
}
.pay-button {
  transition: all 0.3s ease-in;
  font-size: 1.2rem;
  width: 6rem;
  background-color: rgb(27, 196, 27);
  outline: none;
  border-radius: 6px;
  text-transform: uppercase;
  border: 1px solid rgb(143, 139, 139);
}
.fa-amazon-pay {
  position: relative;
  top: 2px;
}

@media (max-width: 522px) {
  p {
    margin: 0;
  }
  .cart-container {
    width: 22rem;
  }
  .addtocart-container .cart-container img {
    width: 180px;
    height: 150px;
  }
  .image-description {
    grid-gap: 2px;
    gap: 2px;
  }
  .add-quantity {
    margin: 2px 0;
  }
  .pay-button {
    width: 85%;
  }
}

.review_page_container {
  display: flex;
  width: 100%;
  font-family: "overpass", "signika negative";
  margin: auto;
  width: 80%;
  margin-bottom: 2rem;
}
/* headings */
.food_detail_heading {
  font-size: 1.4rem;
  font-weight: bold;
}
/* lefrt part of container */
.product_container {
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  max-width: 36rem;
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(218, 215, 215);
  padding: 2rem;
  margin-top: 2rem;
  margin-left: 2rem;
}
.product_image {
  width: 100%;
}
.product_image img {
  height: 100%;
  width: 100%;
  max-width: 100%;
}
.cart_add {
  margin: 1rem 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add_cart_button {
  padding: 0.6rem 0;
  border-radius: 5px;
  width: 100%;
  border: none;
  background-color: #fb641b;
  text-transform: uppercase;
  color: white;
  font-family: "signika negative";
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
}
/* right part of laptop */
.product_summary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 2rem;
}
.product_info_container {
  display: flex;
  width: 100%;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
}
.product_info {
    width: 100%;
    background: #f2f2f2;
    padding: 1rem;
    margin-right: 0.5rem;
}
.product_rating {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    width: 100%;
    background: #f2f2f2;
    padding: 1rem;
}
.detail {
  display: flex;
  align-items: center;
}
.avarage_star {
  font-size: 1rem;
}
/* review from user */
.user_comments_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 0.2rem;
  gap: 0.2rem;
}
.product_review_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
    padding: 1rem;
    background-color: #f2f2f2;
}
.rating_details {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 0.4rem;
  gap: 0.4rem;
}
.star_name {
  display: flex;
  justify-content: center;
  align-items: center;
}
.small-rating-star {
  font-size: 0.8rem;
}
.user_comment {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding: 0.7rem 0 0.8rem 0;
  border-bottom: 1px solid rgb(204, 201, 201);
}
.user_details {
  display: flex;

  justify-content: start;
  grid-gap: 0.4rem;
  gap: 0.4rem;
}
.user_name {
  font-size: 1rem;
  position: relative;
  top: 2px;
}
.upload_images {
  padding-top: 0.4rem;
  max-width: 100%;
  overflow-x: auto;
}
.user_image {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
}

/* uploaded images from user */
.upload_images {
  display: flex;
  align-items: center;
  grid-gap: 0.4rem;
  gap: 0.4rem;
}
.upload_image {
  width: 3rem;
  height: 3rem;
}

/* Responsive Design */
@media (max-width: 1116px) {
  .review_page_container {
    width: 100%;
  }
  .product_info_container {
    display: flex;
    width: 100%;
    align-items: center;
    grid-gap: 0rem;
    gap: 0rem;
    justify-content: space-between;
  }
}

@media (max-width: 859px) {
  .review_page_container {
    flex-direction: column;
    width: 100%;
  }
  .product_info{
    margin-right: 0.7rem;
  }
  .product_container {
    position: static;
    margin: auto;
    width: 100%;
    border: none;
  }
  .product_summary {
    width: 100%;
  }
}
@media (max-width: 513px) {
  .product_info_container {
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
  }
  .rating_container {
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
  }
  .product_info {
    width: 100%;
  }
  .product_rating {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
  }
  .range_input{
    width: 15rem;
  }
  .product_container {
    padding: 0.5rem;
  }
  .product_summary {
    padding: 1rem;
  }
  .food_detail_heading {
    font-size: 1.2rem;
  }
  .cart_add {
    width: 100%;
    position: fixed;
    left: 0;
    top: calc(100vh - 70px);
    z-index: 10;
  }
  .add_cart_button {
    width: 100%;
    border-radius: 0px;
  }
}

@media (max-width: 387px){
    .product_summary {
    padding: 1rem;
}
    .range_input{
        width: 14rem;
    }
}
.order_history_container {
  font-family: "overpass";
  width: 100%;
  height: 100%;
}
.order_heading {
  text-align: center;
  font-size: 1.6rem;
  padding: 1rem 0;
  font-weight: bold;
}
.orders_container {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}
.order {
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  /* align-items: center; */
  min-width: 24rem;
  max-width: 24rem;
  overflow: hidden;
  border: 1px solid gray;
  border-radius: 6px;
}
.order_image_div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.order_image {
  max-width: 10rem;
  max-height: 10rem;
  width: 30vw;
  height: 30vw;
}
.order_info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 0.1rem;
  gap: 0.1rem;
}
.order_info h4,
.order_info span {
  font-size: 1rem;
  font-weight: 500;
}

@media (max-width: 500px) {
    .order{
        min-width: 22rem;
    }
  .order_image {
    width: 9rem;
    height: 9rem;
  }
}

